import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import AddDistrict from "./addDistrict";
import { getMiniStates } from "../../../../store/mini/miniSlice";
import {
  getDistricts,
  districtEdit,
  districtDelete,
  isModelVisible,
  SetDistrictsParams,
} from "../../../../store/Masters/District/districtSlice";

function District(props) {
  const { Search } = Input;
  useEffect(() => {
    props.getDistricts({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "20%",
    },
    {
      title: "District Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "20%",
    },
    {
      title: "State Name",
      dataIndex: "statename",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      width: "20%",
    },
    {
      title: "Actions",
      key: "action",
      width: "5%",
      render: (row, record) => (
        <Space size="middle">
           {props.userAccessList?.indexOf("Masters.change_district") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.districtEdit(row);
              props.getMiniStates({ country: row.country.id });
              props.isModelVisible(true);
            }}
          ></i>
           )}

        {props.userAccessList?.indexOf("Masters.delete_district") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.districtDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.districtslist.map((row, key) => ({
    ...row,
    index:
      (props.districtParams.page - 1) * props.districtParams.page_size +
      key +
      1,
    statename: row.state.name,
  }));

  function onPageChange(page, pageSize) {
    props.SetDistrictsParams({
      ...props.districtParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getDistricts({
      ...props.districtParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetDistrictsParams({ ...props.districtParams, search: val });
    props.getDistricts({ ...props.districtParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }

  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="District Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_district") !== -1 &&
          "Add District"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={12} className="hp-mb-12">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getDistricts({
                      ...props.districtParams,
                      page: 1,
                      search: "",
                    });
                    props.SetDistrictsParams({
                      ...props.districtParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.districtParams?.page}
                pageSize={props.districtParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddDistrict />
    </Row>
  );
}

function mapDistrictToProps({ district }) {
  return {
    districtslist: district.districtslist,
    noofpages: district.noofpages,
    listCount: district.listCount,
    districtParams: district.districtParams,
    loading: district.loading,
  };
}

export default connect(mapDistrictToProps, {
  getDistricts,
  districtEdit,
  districtDelete,
  isModelVisible,
  SetDistrictsParams,
  getMiniStates,
})(District);
