import { Fragment, useState, useEffect } from "react";
import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  districtAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/District/districtSlice";
import { getMiniStates } from "../../../../store/mini/miniSlice";


const AddDistrict = (props) => {
  useEffect(() => {
    props.getMiniStates()
  }, []);

  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const { Option } = Select;
  const onChange = (value) => {
    props.InputChangeValue({
      key: "state_id",
      value: value,
    })
  };
  // Adds New District
  const handleAdd = () => {
    const obj = {
      id: 0,
      state_id: props.selectedData?.state_id,
      name: props.selectedData?.name,
    };
    if (obj.name != null || obj.name == "") {
      props.districtAdd(obj);
    }
  };
  // Updates Districts in Store
  const handleUpdate = () => {
    const districtToUpdate = {
      id: props.selectedData.id,
      state_id: props.selectedData?.state_id == null ? props.selectedData.state.id : props.selectedData?.state_id,
      name: props.selectedData?.name,
    };
    props.districtAdd(districtToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      District
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      state_id: props.selectedData.state_id
    })
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          }
          else {
            handleUpdate();
          }
        }}>
        <Form.Item label="State Name:"
          name="state_id"
          rules={[
            { required: true, message: "Please select your State Name!" },
          ]} >
          <Select
            showSearch
            placeholder="Select State"
            optionFilterProp="children"
            onChange={onChange}
            filterOption={(input, option) =>
              option.children.toLowerCase().includes(input.toLowerCase())
            }
            value={
              props.selectedData.state_id == null
                ? ""
                : props.selectedData?.state_id
            }>
            {props.miniStatesList.map((state, index) => {

              return (
                <Option key={index} value={state.id}>
                  {state.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>


        <Form.Item label="District Name :"
          name="name"
          rules={[
            { required: true, message: "Please input your District Name!" },
          ]}
        >
          <Input
            id="District"
            name="name"
            style={{ width: "100%" }}
            placeholder="District Name"
            value={props.selectedData?.name}
            onChange={ChangeHandeler}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ district, mini }) {
  return {
    selectedData: district.rowdata,
    model: district.model,
    miniStatesList: mini.miniStatesList
  };
}

export default connect(mapStateToProps, {
  districtAdd,
  isModelVisible,
  InputChangeValue,
  getMiniStates
})(AddDistrict);
